import { useEffect, useState } from 'react'
import { useWeb3React } from '@web3-react/core'
import useRefresh from 'hooks/useRefresh'
import { useCyblockPresaleContract } from '../../../hooks/useContract'

const DEFAULT_DATA = {
  packCount: 0,
}

const useGetUserPackCount = (contractAddress) => {
  const { account } = useWeb3React()
  const [data, setData] = useState(DEFAULT_DATA)
  const [isLoading, setIsLoading] = useState(true)
  const { slowRefresh, fastRefresh } = useRefresh()
  const packageContract = useCyblockPresaleContract(contractAddress)

  const getUserPackCount = async () => {
    try {
      const packageIndexes = await packageContract.methods.userPackCount(account).call()
      return packageIndexes
    } catch (error) {
      console.log(error, 'Cannot fetch package index info')
      return null
    }
  }

  useEffect(() => {
    const fetchData = async () => {
      try {
        const numberOfBoughtPackage = await getUserPackCount()
        setData({
          ...data,
          packCount: numberOfBoughtPackage,
        })
        setIsLoading(false)
      } catch (error) {
        setIsLoading(false)
        console.log(error, 'error')
      }
    }
    if (account) {
      fetchData()
    }
  }, [account, fastRefresh])

  return data
}

export default useGetUserPackCount
