import React, { useContext, useMemo, useState } from 'react'
import { CSSTransition, SwitchTransition } from 'react-transition-group'
import { toast } from 'react-toastify'
import get from 'lodash/get'
import { useSelector } from 'react-redux'
import Text from 'components/Text'
import Flex from 'components/Flex'
import LoaderIcon from 'components/LoaderIcon'
import useGetUserPackCount from 'pages/Purchase/hooks/useGetUserPackCount'
import contractConfig from 'config/constant/contract'
import { selectPackProof } from 'state/reducers/app'
import { packageConfig } from 'config/constant'
import { PurchaseContext } from '../../context/PurchaseContext'
import {
  StatsText,
  StyledChangeValueButton,
  StyledClassName,
  StyledClassPercentage,
  StyledClassInput,
  StyledInput,
  PreviewImage,
  PackageWrapper,
  PriceText,
  CardWrapper,
  ItemInfo,
  ItemWrapper,
  TextWrapper,
  StyledButton,
  StyledNumberOfPackage,
  StyledClassInfoHeader,
  PreviewText,
  ProbabilityWrapper,
  PreviewWrapper,
  Video,
} from '../styled'
import theme from '../styled/colors'

const Stats = () => {
  const { common } = theme

  return (
    <ProbabilityWrapper>
      <Text color="#F3F3F3" fontSize="1.6rem" pb="20px">
        Chance to obtain each Class (%)
      </Text>
      <Flex pb="20px" alignItems="center">
        <StyledClassName>Bronze</StyledClassName>
        <StyledClassPercentage>98.30</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={98.3} />
        </StyledClassInput>
      </Flex>
      <Flex pb="20px">
        <StyledClassName>Silver</StyledClassName>
        <StyledClassPercentage>54.94</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={54.9} />
        </StyledClassInput>
      </Flex>
      <Flex pb="20px">
        <StyledClassName>Gold</StyledClassName>
        <StyledClassPercentage>6.53</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={6.53} />
        </StyledClassInput>
      </Flex>
      <Flex pb="20px">
        <StyledClassName>Platinum</StyledClassName>
        <StyledClassPercentage>0.42</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={0.42} />
        </StyledClassInput>
      </Flex>
      <Flex>
        <StyledClassName>Legendary</StyledClassName>
        <StyledClassPercentage>0.07</StyledClassPercentage>
        <StyledClassInput>
          <StyledInput inputBackgroundColor={common.inputBackgroundColor} percentage={0.07} />
        </StyledClassInput>
      </Flex>
    </ProbabilityWrapper>
  )
}

const Preview = () => {
  return (
    <PreviewWrapper justifyContent="center">
      <PreviewImage src="/images/preview-common.png" />
    </PreviewWrapper>
  )
}

const PackageCommon = ({ onChangeValue, onHandleBuyPack, payAmount }) => {
  const {
    cyblockPresale: { commonPackageContractAddress },
  } = contractConfig
  const { state } = useContext(PurchaseContext)
  const [isPreview, setIsPreview] = useState(false)
  const { common: commonPackConfig } = useSelector(selectPackProof)
  const { packCount: commonPackBought } = useGetUserPackCount(commonPackageContractAddress)
  const { numberOfPackage, isLoading } = state
  const { common } = theme

  const limitBoughtPackage = useMemo(() => {
    return get(commonPackConfig, 'maxBuy', null)
  }, [commonPackConfig])

  return (
    <PackageWrapper alignItems="center">
      <ItemWrapper>
        <CardWrapper src={common.cardBackground} />
        <Video muted autoPlay loop>
          <source src="/videos/5-Pack-Common.webm" type="video/webm" />
        </Video>
        <TextWrapper>
          <Flex>
            <StyledChangeValueButton
              color="#F3F3F3"
              fontSize="60px"
              fontWeight="600"
              onClick={() => onChangeValue('down')}
            >
              -
            </StyledChangeValueButton>
            <StyledNumberOfPackage>{numberOfPackage}</StyledNumberOfPackage>
            <StyledChangeValueButton
              color="#F3F3F3"
              fontSize="6rem"
              fontWeight="600"
              onClick={() => {
                if (limitBoughtPackage !== numberOfPackage) {
                  onChangeValue('up')
                }
              }}
              disabled={limitBoughtPackage === numberOfPackage}
            >
              +
            </StyledChangeValueButton>
          </Flex>
        </TextWrapper>
        <PriceText>3 BNB (~1000$)</PriceText>
        <StyledButton
          buttonBackgroundColor={common.buttonBackgroundColor}
          buttonDisabled={common.buttonDisabled}
          isLoading={isLoading}
          onClick={() => {
            if (Number(commonPackBought) === limitBoughtPackage) {
              toast.error('You already reach limit', {
                hideProgressBar: true,
              })

              return
            }

            if (!isLoading) {
              onHandleBuyPack()
            }
          }}
        >
          {isLoading ? (
            <LoaderIcon />
          ) : (
            <Text color="#110A19" fontWeight="700">
              Buy for 3 BNB
            </Text>
          )}
        </StyledButton>
      </ItemWrapper>
      <ItemInfo cardBackgroundColor={common.cardBackgroundColor}>
        <StyledClassInfoHeader>
          <PreviewText isPreview={isPreview} onClick={() => setIsPreview(true)}>
            Preview
          </PreviewText>
          <StatsText isPreview={isPreview} onClick={() => setIsPreview(false)}>
            Probability
          </StatsText>
        </StyledClassInfoHeader>
        <SwitchTransition mode="out-in">
          <CSSTransition
            key={isPreview}
            addEndListener={(node, done) => {
              node.addEventListener('transitionend', done, false)
            }}
            classNames="fade"
          >
            {isPreview ? <Preview /> : <Stats />}
          </CSSTransition>
        </SwitchTransition>
      </ItemInfo>
    </PackageWrapper>
  )
}

export default PackageCommon
