import { selectPackProof } from 'state/reducers/app'
import get from 'lodash/get'
import Box from 'components/Box'
import contractConfig from 'config/constant/contract'
import useGetUserPackCount from 'pages/Purchase/hooks/useGetUserPackCount'
import LoaderIcon from 'components/LoaderIcon'
import { packageConfig } from 'config/constant'
import { PurchaseContext } from '../../context/PurchaseContext'
import {
  StatsText,
  StyledChangeValueButton,
  StyledClassName,
  StyledClassPercentage,
  StyledClassInput,
  StyledInput,
  PreviewImage,
  PackageWrapper,
  PriceText,
  Video,
  CardWrapper,
  ItemInfo,
  ItemWrapper,
  TextWrapper,
  StyledButton,
  StyledNumberOfPackage,
  StyledClassInfoHeader,
  PreviewText,
  ProbabilityWrapper,
  PreviewWrapper,
} from '../styled'
import theme from '../styled/colors'

