const Preview = () => {
  return (
    <PreviewWrapper justifyContent="center">
      <PreviewImage src="/images/preview-common.png" />
    </PreviewWrapper>
  )
}

const PackageEpic = ({ onChangeValue, onHandleBuyPack, payAmount }) => {
  const {
    cyblockPresale: { epicPackageContractAddress },
  } = contractConfig
  const { state } = useContext(PurchaseContext)
  const { epic: epicPackConfig } = useSelector(selectPackProof)
  const [isPreview, setIsPreview] = useState(false)
  const { packCount: epicPackCount } = useGetUserPackCount(epicPackageContractAddress)
  const { numberOfPackage, isLoading } = state
  const { epic } = theme

  const limitBoughtPackage = useMemo(() => {
    return get(epicPackConfig, 'maxBuy', null)
  }, [epicPackConfig])

  return (
    <PackageWrapper alignItems="center">
      <ItemWrapper>
        <CardWrapper src={epic.cardBackground} />
        <Video muted autoPlay loop>
          <source src="/videos/5-Pack-Epic.webm" type="video/webm" />
        </Video>
        {/* <TextWrapper>
          <Flex>
            <StyledChangeValueButton
              color="#F3F3F3"
              fontSize="6rem"
              fontWeight="600"
              onClick={() => onChangeValue('down')}
            >
              -
            </StyledChangeValueButton>
            <StyledNumberOfPackage>{numberOfPackage}</StyledNumberOfPackage>
            <StyledChangeValueButton
              color="#F3F3F3"
              fontSize="6rem"
              fontWeight="600"
              onClick={() => onChangeValue('up')}
              disabled={limitBoughtPackage === numberOfPackage}
            >
              +
            </StyledChangeValueButton>
          </Flex>
        </TextWrapper> */}
        <PriceText>3 BNB (~1000$)</PriceText>
        <StyledButton
          buttonBackgroundColor={epic.buttonBackgroundColor}
          buttonDisabled={epic.buttonDisabled}
          isLoading={isLoading}
          onClick={() => {
            if (Number(epicPackCount) === limitBoughtPackage) {
              toast.error('You already reach limit', {
                hideProgressBar: true,
              })

              return
            }

            if (!isLoading) {
              onHandleBuyPack()
            }
          }}
        >
          {isLoading ? (
            <LoaderIcon />
          ) : (
            <Text color="#110A19" fontWeight="700">
              Buy for 3 BNB
            </Text>
          )}
        </StyledButton>
      </ItemWrapper>
      <ItemInfo cardBackgroundColor={epic.cardBackgroundColor}>
        <StyledClassInfoHeader>
          <PreviewText isPreview={isPreview} onClick={() => setIsPreview(true)}>
            Preview
          </PreviewText>
          <StatsText isPreview={isPreview} onClick={() => setIsPreview(false)}>
            Probability
          </StatsText>
        </StyledClassInfoHeader>
        <SwitchTransition mode="out-in">
          <CSSTransition
            key={isPreview}
            addEndListener={(node, done) => {
              node.addEventListener('transitionend', done, false)
            }}
            classNames="fade"
          >
            {isPreview ? <Preview /> : <Stats />}
          </CSSTransition>
        </SwitchTransition>
      </ItemInfo>
    </PackageWrapper>
  )
}

export default PackageEpic
